.acc-mobile {
    display: none;
}

.tabletizers-wrp {
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.04);
    padding: 40px;
    margin-top: -100px;
    position: relative;
    z-index: 50;

    p {
        color: $fontd;
        margin-bottom: 30px;
    }
}

.tabletiz {
    display: flex;
    flex-direction: column;

    &__row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

    }

    &__col {
        display: flex;
        padding: 5px;
        align-items: center;
        flex-basis: 25%;
        justify-content: center;
        margin: 2px;
        height: 56px;
    }

    .col_titleleft {
        justify-content: flex-start;
        line-height: 1.2;
    }

    &__ic {
        width: 23px;
        height: 23px;
    }


    .col_titletop {
        background: $blue;
        color: #fff;
        justify-content: center;
    }

    .col_darken {
        background: #EFF1F4;
    }

}

.ic_faq {
    width: 26px;
    height: 26px;
}

.accordion-menu {

    max-width: 100%;
    margin-top: 30px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0px 5px 12px #ececec;
    border-radius: 8px;
}

.accordion-menu li.open .dropdownlink {
    color: $blue;
}

.accordion-menu li:last-child .dropdownlink {
    border-bottom: 0;
}

.dropdownlink {
    cursor: pointer;
    display: block;
    padding: 20px 15px 20px 15px;
    font-size: 16px;
    border-bottom: 1px solid #e8e8e8;
    color: $fontd;
    font-weight: 600;
    position: relative;
    transition: all 0.4s ease-out;
    display: flex;
    justify-content: space-between;

    span {
        padding-right: 20px;
        line-height: 1.3;
    }


}

.ic_faq {
    transform: rotate(45deg);
    fill: $fontl;
}

.accordion-menu li.open .dropdownlink {
    .ic_faq {
        transform: rotate(90deg);
        fill: $blue;
    }

}

.question_info {
    display: none;
    background: #f3f3f3;
    padding: 15px;
    line-height: 1.5;
    font-size: 15px;
    color: $fontl;



}

.submenuItems a {
    display: block;
    color: #727272;
    padding: 15px 12px 15px 45px;
    transition: all 0.3s ease-out;
    text-decoration: none;

    &:hover {
        background-color: #fff;
        color: #727272;
    }
}

.table-mob {
    display: flex;
    flex-direction: column;

    &__row {
        display: flex;
        flex-direction: row;
    }

    &__col {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-basis: 33%;
        padding: 15px;
        margin: 2px;
        height: 50px;


    }

    &__col--title {
        background: $blue;
        color: #fff;
        font-size: 12px;
        line-height: 1.3;
        text-align: center;
    }

    &__ic {
        width: 20px;
        height: 20px;
    }
}


@media only screen and (max-width : 737px) {
    .tabletizers {
        display: none;
    }

    .acc-mobile {
        display: block;
    }

    .tabletizers-wrp {
        background: #FFFFFF;
        box-shadow: none;
        padding: 0px;
        margin-top: 0px;

        p {
            margin-bottom: 0px;
        }

    }

    .accordion-menu {
        margin-top: 15px;
    }
}
