.tizers {
    background: #2D3C5E;
    margin: 150px 0 0 0;
    position: relative
}

.tizers_wrp {

    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

.tizer {
    padding: 80px 10px 190px 10px;
    position: relative;
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__number {
        position: absolute;
        font-weight: 800;
        font-size: 160px;
        color: rgba(255, 255, 255, 0.05);
        top: 0px;
        right: 30px;
        z-index: 1;
    }

    &__ic {
        width: 70px;
        height: 70px;
        margin-bottom: 20px;
    }

    &__title {
        color: #fff;
        text-align: center;
    }
}

.tizer2 {
    background: #334368;
}

.tizer3 {
    background: #273759;
}

@media only screen and (max-width : 768px) {

    .tizers {
        background: transparent;
        margin: 50px 0;

    }

    .tizer1 {
        background: #2D3C5E;
    }

    .tizer4 {
        background: #2D3C5E;
    }

    .tizer {
        padding: 30px 10px 30px 10px;
        flex-basis: 50%;


    }
}

@media only screen and (max-width : 576px) {
    .tizers {
        margin: 10px 0 50px 0;

    }

    .tizer {

        &__number {
            font-size: 90px;

        }

        &__ic {
            width: 40px;
            height: 40px;
            margin-bottom: 20px;
        }

        &__title {
            color: #fff;
            text-align: center;
            font-size: 13px;
        }
    }

}
