.arrows-wrp {
    display: flex;
    margin-top: 40px;
}

.arrows {
    display: flex;
}

.btn-gold {

    height: 60px;
    border-radius: 4px;
    background: $gold;
    color: #fff;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    border: none;

    &:hover {
        color: #fff;
        background: $blue;
    }
}

.pr-prev,
.pr-next {
    border: none;
    padding: 10px 15px;
    background: $blue;
    transition: all .3s;
    color: #fff;
    width: 60px;
    height: 60px;
    position: relative;
    padding: 5px;
    text-align: center;
    border-radius: 4px;
    margin-right: 4px;


    &:hover {
        opacity: .7;
    }

    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 13px;
        height: 13px;


    }

}

.pr-next {


    &:after {
        background: url('../img/ic-right.svg');
        background-repeat: no-repeat;

    }
}

.pr-prev {
    &:after {
        background: url('../img/ic-left.svg');
        background-repeat: no-repeat;
    }
}

.planes-slider {
    margin-right: -300px;
    margin-left: -20px
}

.plane {
    position: relative;
    background: #EBEBEB;
    border-radius: 4px;
    overflow: hidden;
    margin: 0 20px;

    &__img {
        display: block;
        z-index: 20;
        position: relative;
        height: 200px;
        text-align: center;

    }

    &__bg {
        position: absolute;
        top: 100px;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 1;
    }

    &__title {
        font-size: 24px;
        font-weight: 700;
        color: $fontd;
        z-index: 20;
        position: relative;
        text-align: center;
        padding: 0 15px;
    }

    &__info {
        position: relative;
        z-index: 20;
        margin: 20px 0;
    }

    .pinfo {
        display: flex;
        justify-content: space-between;
        padding: 7px 15px;
        border-bottom: 1px solid #ddd;



        &__title {
            font-size: 14px;
            color: $fontl;
        }
    }

    &__btns {
        display: flex;
    }

    .btn-plane {
        color: #fff;
        transition: all .3s;
        flex-basis: 50%;
        padding: 20px 10px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .3s;

    }

    .btn_arenda {
        background: $blue;

        &:hover {
            background: darken($blue, 5%);
        }

    }

    .btn_more {
        background: $gold;

        &:hover {
            background: darken($gold, 5%);
        }

    }

    .ic_btn {
        width: 17px;
        height: 17px;
        fill: #fff;
        margin-left: 10px;
        margin-top: 5px;
    }
}

.plane-catalog {
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 20px;
}

@media only screen and (max-width : 1200px) {
    .planes {
        padding: 40px 0;
    }

    .planes-slider {
        margin-right: 0px;
        margin-left: 0;

    }

}

@media only screen and (max-width : 991px) {
    .planes-slider {
        margin: 0 -5px;
    }

    .plane {
        margin: 0 5px;
    }
}

@media only screen and (max-width : 506px) {
    .plane {
        margin: 0 0;
    }

    .plane-catalog {
        margin-bottom: 20px;
    }
}
