.timetable {
    margin-top: 50px;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 100px;
}

.nav-item {
    flex-basis: 50%;
}

.nav-tt {
    .nav-link {
        background: $gold;
        outline: none;
        border: none;
        box-shadow: none;
        border-radius: 0 0 0 0;
        color: #fff;
        padding: 15px 30px;
        font-weight: 700;
        width: 100%;

        &:hover {
            border: none;
            box-shadow: none;
        }
    }

    .active {
        background: #fff;
        border-radius: 0 0 0 0;
        color: $fontd;
        padding: 15px 30px;
    }

}

.tab-content {
    background: #fff;
    padding: 20px 30px;
}

.mytable {
    display: flex;
    flex-direction: column;


    &__row {
        display: flex;
        flex-direction: row;
        margin-bottom: 2px;
        color: $fontd;
        transition: all .3s;


        opacity: .6;

        &:hover {

            opacity: 1;
            color: $gold;
        }

    }

    .col-first {
        flex-basis: 22%;
    }

    .col-second {
        flex-basis: 20%;
    }

    .col-third {
        flex-basis: 10%;
    }

    .col-forth {
        flex-basis: 20%;
    }

    .col-fifth {
        flex-basis: 28%;
    }


    &__col {
        padding: 10px 20px;
        display: flex;
        justify-content: center;
        align-items: center
    }

    .col-first {
        justify-content: flex-start;
    }

    .col-darken {
        background: #F0F3F9;

    }

    .col-lighten {
        background: #FAFCFF;
    }

    .table-date,
    .table-from,
    .table-to {
        display: flex;
        flex-direction: column;

        &__title {
            font-size: 12px;
            color: $fontd;
            font-weight: 700;
        }

        &__info {


            font-size: 12px;
            color: $fontl;
            margin-bottom: 2px;
        }
    }

    .ic_table {
        width: 16px;
        height: 16px;
    }

    .table-price {
        font-weight: 800;
        font-size: 16px;
    }

}

.btn-blue {
    padding: 20px 10px;
    width: 100%;
    background: $blue;
    color: #fff;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all .3s;

    &:hover {
        background: $gold;
    }
}

@media only screen and (max-width : 504px) {
    .tab-content {
        padding: 20px 10px;
    }

    .nav-tt {
        .nav-link {
            padding: 15px 15px;
        }

        .active {
            padding: 15px 15px;
        }

    }

    .mytable__col {
        padding: 7px 5px;

    }

    .mytable .table-price {
        font-size: 12px;
    }
}
