.main-text {
    background: #fafafa;
    padding: 20px 15px 20px 40px;
    border-left: 3px solid $blue;
    line-height: 1.5;
    margin: 20px 0;
}

article {
    padding: 30px 0;



    h3 {
        font-size: 22px;
        font-weight: 600;
        line-height: 1.5;

    }

    p {
        line-height: 1.5;
        padding: 5px 0;
    }


    ul,
    ol {
        padding: 5px 10px 5px 40px;

        line-height: 1.5;
        list-style-type: disc;

        li {
            padding: 5px 0;

        }
    }

    ol {
        list-style-type: decimal;

        li {
            padding-left: 7px;
        }
    }
}
