.filter_items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.mix {
    display: none;
    flex-basis: 32%;
}

@media only screen and (max-width : 845px) {
    .mix {
        flex-basis: 48%;
    }
}

@media only screen and (max-width : 561px) {
    .mix {
        flex-basis: 100%;
    }
}
