.main-form {
    margin-top: 80px;
    padding: 20px;
    background: $blue;
}

.input_wrp {
    position: relative;

    label {
        color: #fff;
        font-weight: 400;
        margin-bottom: 5px;
        opacity: .5;
    }

    input,
    select,
    textarea {
        background: #F0F3F9;
        border: 1px solid #ffffff17;
        width: 100%;
        transition: all .9s;
        padding: 20px 20px 20px 20px;
        color: $fontl;
        border-radius: 4px;
        margin-bottom: 20px;


        &:focus {
            outline: none;
            border: 1px solid $gold;
        }

    }

    select {
        line-height: 1.5;
        padding: 23px 10px 23px 70px;

    }

    ::-webkit-input-placeholder {
        color: $fontl;
    }

    ::-moz-placeholder {
        color: $fontl
    }

    :-ms-input-placeholder {
        color: $fontl
    }

    ::-ms-input-placeholder {
        color: $fontl
    }

    ::placeholder {
        color: $fontl
    }

    ::-moz-selection {

        color: $fontl
    }

    ::selection {

        color: $fontl
    }

    /*    ::-webkit-calendar-picker-indicator {
        filter: invert(1);
    }*/


}

.ic_form {
    position: absolute;
    right: 15px;
    top: 52px;
    width: 20px;
    height: 20px;
    fill: #0B1F33;
}

.ic_form2 {
    left: 30px;

}


.filters-check {

    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;
    opacity: 1 !important;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    .checkbox-wrap {

        display: flex;

        align-items: center;

        justify-content: center;
        width: 20px;
        max-width: 20px;

        flex: 0 0 20px;
        height: 20px;
        border-radius: 2px;
        border: 2px solid #e7e7e7;

        &::after {
            content: '';
            display: block;
            width: 12px;
            height: 10px;
            background: url("../img/svg-check-ic.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;

            transform: scale(0);
            opacity: 0;

            transition: all 0.1s linear;
        }
    }

    .checkbox-text {
        display: inline-block;
        margin-top: 1px;
        margin-left: 14px;
        font-size: 14px;
    }

    input {
        color: transparent;
        text-shadow: 0 0 0 black;

        &:checked {
            + {
                .checkbox-wrap {
                    &::after {

                        transform: scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.filters-row {

    display: flex;

    flex-wrap: wrap;
}

.checkbox-hidden {
    display: none;
}

.ui-quantity {
    display: flex;
    align-items: center;
    height: 50px;
    background-color: #fff;
    border-radius: 2px;
    border: 1px solid #e7e7e7;
    transition: all 0.1s linear;
    margin-bottom: 30px;
    width: 50%;

    input {
        width: 50%;
        min-width: 1px;
        background: #fff;
        border: 1px solid #ffffff17;
        width: 100%;
        transition: all .9s;
        color: $fontl;
        border-radius: 4px;
        height: 100%;
        background: #f0f3f9;
        padding-left: 15px;
    }

    button {
        display: flex;
        justify-content: center;
        width: 25%;
        min-width: 25%;
        border-radius: 2px;
        opacity: 0.8;
        transition: all 0.1s linear;
        align-items: center;
        background: transparent;
        border: none;

        .plus {
            fill: gray;
            width: 18px;
            height: 18px;
        }
    }

    .ic_quant {
        width: 18px;
        height: 10px;
        fill: $fontl;
    }

}

.btn-gold {
    border: 1px solid $gold;
    background: $gold;
    color: #ffff;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s;
    padding: 20px 10px;
    width: 100%;
    font-size: 18px;
    font-weight: 700;
    border-radius: 4px;
    margin-bottom: 10px;
    display: block;

    &:hover {
        background: transparent;
        border: 1px solid $gold;

    }

}

.ic_lock {
    width: 10px;
    height: 10px;
    fill: #fff;
    opacity: .6;
    margin-bottom: 5px;
}

.sogl {

    font-size: 12px;
    color: #fff;
    opacity: .6;
}

@media only screen and (max-width : 504px) {
    .main-form {
        margin-top: 0px;

    }
}
