.header-top {
    top: -220px;
}

.fixed {
    transition: top 1s;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 60;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 1320px;
    padding: 0 15px;
    z-index: 100;

    &::before {
        content: "";
        width: 3000px;
        height: 80px;
        background: #fff;
        position: absolute;
        top: 0;
        left: -1000px;
        display: block;
        z-index: -1;
    }

    .header__logo {

        padding-top: 8px;
        padding-bottom: 8px;
        padding-right: 20px;


        img {
            width: 60px;
            height: 60px;
        }

        &::before {
            height: 80px;
            width: 1000px;

        }

    }

    .header__top {
        padding-top: 15px;

        &::after {
            background: transparent;

        }
    }

    .header__descriptor {

        font-size: 12px;
        line-height: 1.3;
        color: $fontl;
        max-width: 400px;

    }

    .header__phone {

        .ic_header {
            width: 15px;
            height: 15px;
            fill: $fontd;
        }

    }

    .header__phone-number {
        font-size: 18px;
        color: $fontd;
        margin-bottom: 0;

        &:hover {
            color: $fontd;
        }
    }

    .header__email {
        font-size: 14px;
        color: $fontl;
        opacity: 1;

        &:hover {
            color: $fontl;
            opacity: 1;
        }
    }

    .header__button {
        border: 1px solid $gold;
        padding: 10px 40px;
        color: $fontd;

        &:hover {

            color: #fff;
            background: $gold;
            border: 1px solid $gold;
        }
    }

    .header__top-menu {

        padding: 0;
        margin: 0;
        line-height: 1;
    }

    .sf-menu li a {
        color: $fontd !important;
        font-size: 13px;
        padding: 0px 20px 0 0;

        &:hover {
            color: $gold !important;
        }

    }

    .sf-menu li {
        &:hover {
            border: none;
        }
    }

    .wrapper {
        line-height: 1;
    }

    .header-wrap {
        margin-top: -22px;
    }

    .toggle-mnu span,
    .toggle-mnu span::after,
    .toggle-mnu span::before {
        background-color: $fontd;
    }


}

.mfixed {

    margin-top: 180px;
}



@media only screen and (max-width : 990px) {
    .fixed {

        &::before {
            content: "";
            width: 2000px;
            height: 50px;

        }

        .header-wrap {
            margin-top: 0px;
        }

        .header__logo {

            padding-top: 8px;
            padding-bottom: 8px;
            padding-right: 20px;


            img {
                width: 38px;
                height: 38px;
            }

            &::before {
                height: 50px;
                width: 1000px;

            }

        }

        .header__top {
            padding-top: 10px;
        }

    }
}
