.all-btn {
    display: flex;
    justify-content: flex-end;
}

.btn-news {
    display: inline-flex;
    width: auto;
    padding-left: 30px;
    padding-right: 30px;
    border: 1px solid $gold;

    &:hover {
        border: 1px solid $blue;

    }
}

.news {
    margin: 60px 0;

    .block-title {
        margin-bottom: 0px;
    }

    &__item {
        border: 1px solid #ececec;
        margin-bottom: 30px;
    }

    &__img {
        position: relative;
        width: 100%;
        display: block;

        img {
            display: block;
        }
    }

    &__date {
        position: absolute;
        left: 0;
        bottom: 0;
        background: $blue;
        color: #fff;
        padding: 7px 14px;
    }

    &__title {
        padding: 15px;
        color: $fontd;
        font-weight: 700;
        display: block;

        &:hover {
            color: $gold;
        }

    }

    &__info {
        padding: 15px;
    }

    .img-sm {
        overflow: hidden;


        img {
            width: 100%;
            height: 174px;
            object-fit: cover;
        }
    }


}

@media only screen and (max-width : 768px) {

    .news {

        &__item {
            width: 100%;
        }

    }
}
