.mobile_wrp {
    display: none;
}

@media only screen and (max-width : 991px) {
    .mobile_wrp {
        display: block;
    }

    .mobile_buttons {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin: 0 15px;

        a {
            background: $gold;
            width: 30px;
            height: 30px !important;
            margin-right: 2px;
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 4px;
        }

        .ic_mob {
            width: 15px;
            height: 15px;
            fill: #fff;
        }
    }
}
