.header-wrp {
    background: url('../img/bg.jpg');
    padding-bottom: 170px;
}

.header-page {
    padding-bottom: 0;
}

.header-page {
    height: 181px;
    background: #3f4e70;
}

@media only screen and (max-width : 576px) {
    .header-page {
        height: 80px;
    }

}

.header {
    display: flex;
    justify-content: space-between;

    &__logo {
        position: relative;
        z-index: 10;
        flex-basis: 15%;
        padding-top: 18px;
        padding-bottom: 18px;
        padding-right: 50px;


        img {
            width: 141px;
            height: 135px;
        }

        &::before {
            content: "";
            position: absolute;
            z-index: -5;
            height: 180px;
            width: 1000px;
            background: $gold;
            right: 0;
            top: 0;
        }

    }

    &__top {
        display: flex;
        flex-direction: column;
        flex-basis: 85%;
        padding-top: 32px;
        padding-left: 23px;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: -1000px;
            width: 3000px;
            height: 1px;
            background: #fff;
            opacity: .5;
        }
    }

    &__top-contacts {
        display: flex;
        justify-content: space-between;
    }

    &__descriptor {
        max-width: 300px;
        font-size: 14px;
        line-height: 1.5;
        color: #fff;
        opacity: .5;

    }

    &__contacts {
        display: flex
    }

    &__phone {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-right: 20px;

        .ic_header {
            width: 22px;
            height: 22px;
            margin-right: 10px;
            display: inline-block;
            margin-bottom: 5px;
            fill: #fff;
        }

    }

    &__phone-number {
        font-size: 24px;
        color: #fff;
        font-weight: 700;
        display: block;
        margin-bottom: 5px;

        &:hover {
            color: #fff;
        }
    }

    &__email {
        font-size: 18px;
        color: #fff;
        opacity: .5;

        &:hover {
            color: #fff;
            opacity: 1;
        }
    }

    &__button {
        border: 1px solid #fff;
        padding: 10px 40px;
        color: #fff;
        transition: 0.2s 0.1s;
        display: flex;
        justify-content: center;
        align-items: center;
        background: transparent;
        border-radius: 4px;
        cursor: pointer;

        &:hover {
            color: #fff;
            background: $gold;
            border: 1px solid $gold;
        }
    }
}

.main__heading {
    padding-top: 11px;

    h1 {
        font-family: "Forum", sans-serif;
        color: #fff;
        font-size: 64px;
        padding-top: 111px;
        line-height: 1;
        margin-bottom: 20px;
    }

    p {
        font-size: 20px;
        color: #fff;
    }
}

@media only screen and (max-width : 991px) {
    .header {


        &__contacts {
            display: none;
        }

        &__top {
            display: flex;
            flex-direction: row;
            flex-basis: 85%;
            padding-top: 32px;
            padding-left: 23px;
            justify-content: flex-end;


            &::after {
                display: none;
            }

        }

        &__logo {
            position: relative;
            z-index: 10;
            flex-basis: 15%;
            padding-top: 10px;
            padding-bottom: 1px;
            padding-right: 18px;

            img {
                width: 100%;
                height: auto;
            }

            &::before {
                height: 130px;

            }

        }

    }
}

@media only screen and (max-width : 504px) {
    .header-wrp {
        background: url('../img/bg.jpg');
        padding-bottom: 30px;
    }

    .header {
        &__descriptor {
            display: none;

        }

        &__logo {
            flex-basis: 25%;

            img {
                width: 60px;
                height: 60px;
            }

            &::before {
                height: 80px;

            }

        }

        &__top {
            flex-basis: 75%;
            padding-left: 23px;
            padding-top: 15px
        }

    }

    .main__heading {

        h1 {
            font-size: 28px;
            padding-top: 60px;
        }

        p {
            font-size: 16px;
        }
    }

}
