.heading_photos {
    padding-right: 150px;
    text-transform: inherit;
}

.slider_container {
    position: relative;
    margin-top: 30px;

    img {
        width: 100%;
        height: 316px;
        object-fit: cover;
        border-radius: 8px;
    }
}

.slider_photos {
    margin: 0 -15px;

}

.slider_photos__page {
    margin: 0 -5px;

}

.gal_wrp {
    margin: 15px;
    border-radius: 8px;
    /*  overflow: hidden;*/
    background: #fff;
    transform-style: preserve-3d;
    position: relative;
    box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.08);
    transition: all .3s;
    margin-bottom: 20px;
    position: relative;
    margin-bottom: 40px;




    &:after {
        content: "";
        position: absolute;
        width: 96%;
        height: 30px;
        background-color: #fff;
        left: 2%;
        bottom: -8px;
        transform: translateZ(-1px);
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
        border-radius: 15px;

    }

    &:hover {
        box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.08);

    }
}

.btn-offer {
    width: 300px;
    margin: 50px 0;
    display: block;
    text-align: center;
    cursor: pointer;
}

.gal_wrp__page {
    margin: 5px 5px 40px 5px;
}



.gal_info {
    padding: 15px;

    .gal_title {
        font-family: Montserrat-BlackItalic;
        font-size: 19px;
        margin-bottom: 15px;
    }

    .gal_title__page {
        font-family: inherit;
        font-size: 18px;
        font-weight: 700;

    }

    a {
        font-size: 15px;

    }

    i {
        color: $gold;
    }
}

.case_page {
    margin-bottom: 50px;


    .case_title_page {
        color: darkgray;
        font-size: 14px;
        font-weight: 300;
        margin-bottom: 10px;

    }
}


@media only screen and (max-width : 992px) {
    .heading_photos {
        padding-right: 0px;

    }

}
