.cloud {
    margin-top: 80px;
    overflow: hidden;
    position: relative;
    width: 100%;
    background: url('../img/bg2.jpg');
    background-repeat: no-repeat;
    height: 707px;
    background-size: cover;

}


.cloud img {
    width: 100%;
    left: 0;
    top: 0;
    position: absolute;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    pointer-events: none;
    z-index: 10;
    opacity: .5;
}

@-webkit-keyframes animCloud {
    from {
        -webkit-transform: translateX(100%)
    }

    to {
        -webkit-transform: translateX(-100%)
    }
}

@-moz-keyframes animCloud {
    from {
        -moz-transform: translateX(100%)
    }

    to {
        -moz-transform: translateX(-100%)
    }
}

@keyframes animCloud {
    from {
        -webkit-transform: translateX(-100%);
        -moz-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
        -o-transform: translateX(-100%);
        transform: translateX(-100%)
    }

    to {
        -webkit-transform: translateX(100%);
        -moz-transform: translateX(100%);
        -ms-transform: translateX(100%);
        -o-transform: translateX(100%);
        transform: translateX(100%)
    }
}

@keyframes animCloud2 {
    from {
        -webkit-transform: translateX(100%);
        -moz-transform: translateX(100%);
        -ms-transform: translateX(100%);
        -o-transform: translateX(100%);
        transform: translateX(100%)
    }

    to {
        -webkit-transform: translateX(-100%);
        -moz-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
        -o-transform: translateX(-100%);
        transform: translateX(-100%)
    }
}

.cloud1 {
    -webkit-animation: animCloud2 70s infinite linear;
    -moz-animation: animCloud2 70s infinite linear;
    animation: animCloud2 70s infinite linear
}

.cloud2 {
    -webkit-animation: animCloud 40s infinite linear;
    -moz-animation: animCloud 40s infinite linear;
    animation: animCloud 40s infinite linear
}

.cloud3 {
    -webkit-animation: animCloud 60s infinite linear;
    -moz-animation: animCloud 60s infinite linear;
    animation: animCloud 60s infinite linear
}

.cloud4 {
    -webkit-animation: animCloud 80s infinite linear;
    -moz-animation: animCloud 80s infinite linear;
    animation: animCloud 80s infinite linear
}

.about_wrp {
    display: flex;
    justify-content: space-between;

    p,
    .block-title,
    .about__info {
        position: relative;
        z-index: 50;
    }

}

.about__title {
    flex-basis: 30%;


}

.about__info {
    flex-basis: 40%;
    padding: 30px;
    background: #fff;

}

@media only screen and (max-width : 992px) {
    .about_wrp {
        flex-wrap: wrap;
    }

    .about__title {
        flex-basis: 100%;
    }

    .about__info {
        padding: 15px;
        flex-basis: 100%;
        background: #1a1631a6;
        margin-top: 20px;

        p {
            color: #fff;
        }

    }

    .cloud {
        height: auto;
    }

    .cloud img {

        opacity: 1;
        top: 200px;
    }

}
